import './Publish.scss';
import {useState } from 'react';
import { CreateCollectService } from '../../services/PublishService';
import { showError, showLoading, showSuccess } from "../../components/Message";
import { publishStore } from '../../stores/PublishStore';
import { Modal } from 'antd';

interface VideoCollecProps {
    visible: boolean;
    cancle: Function;
}


export const VideoCollectModal = (props:VideoCollecProps) => {
   const [title,setTitle] =useState('',);
   var  creatCollect=async ()=>{
       console.log("title",title)
       showLoading('提交中...');
    //    try{
           await new CreateCollectService(title).send();
    //    }catch (e){
    //        showError(`${e}`);
    //        return ;
    //    }
       showSuccess("创建成功");
           var input: any = document.getElementById('collection');
        input.value = '';
        publishStore.getCollections();
   }
    
    return (

        <Modal title="视频合集" visible={props.visible}
        width={700}
        footer={null}
        centered={true}
        onCancel={() => { props.cancle() }}
    >
         <div className="video-collection" >
            <div className="byte-form-item" style={{ marginTop: '40px' }}>
                <div className="byte-row byte-form-layout-horizontal">
                    <div className="byte-form-label-item byte-col byte-col-4">
                        <label> 合集标题</label>
                    </div>
                    <div className="byte-form-item-wrapper byte-col byte-col-18">
                        <div className="byte-form-item-control" id="description">
                            <div className="byte-form-item-wrapper-children">
                                <textarea id="collection" style={{ height: '50px!important' }} className="byte-textarea inputwrapper" onChange={(e) => {
                                    setTitle(e.target.value);
                                }} placeholder="请输入合集标题">
                                </textarea>
                            </div>
                        </div>

                    </div>
                </div>
            </div>

            <button className={`publish-button ${title.length>0?'active':''}`} onClick={() => {
                creatCollect();
            }}><span
                className="_1Qjd7">创建合集</span></button>
        </div>
    </Modal>

    
    )
};