
import { AboutUs } from "../pages/AboutUs/AboutUs";
import { CreatorPage } from "../pages/Creator/Creator";
import { CreatorDetail } from "../pages/Creator/CreatorDetail";
import Index from "../pages/Index";
import { Kline } from "../pages/Kline/Kline";
import { Main } from "../pages/Main/Main";
import { MarketCalendar } from "../pages/MarketCalendar/MarketCalendar";
import  { NewsPage } from "../pages/News/News";
import { PublishNewsPage } from "../pages/Publish/PublishNews";
import {  PublishVideoPage } from "../pages/Publish/PublishVideo";
import { Research } from "../pages/Research/Research";
import { ResearchDetail } from "../pages/Research/ResearchDetail";
import { StockIndex } from "../pages/Stock/StockIndex";
import { StockSearch } from "../pages/Stock/StockSearch";
import { WatchListPage } from "../pages/Stock/WatchList/WatchList";
import { Subscribe } from "../pages/Subscribe/Subscribe";
import { Videos } from "../pages/Videos/Videos";
import { CreatorVideos } from "../pages/VideosCreator/Videos";





export const routesConfig = [
    {
        path: "/",
        component: Index,
        routes: [
            {
                path: "/",
                exact: true,
                component: Main
            },
            {
                path: "/news",
                component: NewsPage
            },
            {
                path: "/aboutUs",
                component: AboutUs
            },
            {
                path: "/research",
                component: Research,
                rule: 'researchReport'
            }, {
                path: "/researchDetail/:id",
                component: ResearchDetail,
                rule: 'researchReport'
            }
            , {
                path: "/subscribe",
                component: Subscribe,
            },

            {
                path: "/videos",
                component: Videos
            },
            {
                path: "/watchList",
                component: WatchListPage,
            },
            {
                path: "/stockSearch",
                component: StockSearch,
            }
            ,
            {
                path: "/stockDetail/:symbol",
                component: StockIndex,
            } ,
            {
                path: "/publishVideo",
                component: PublishVideoPage,
            },
            {
                path: "/publishNews",
                component: PublishNewsPage,
            },
            {
                path: "/creator",
                component: CreatorPage,
            },
            {
                path: "/creatorNewsDetail",
                component: CreatorDetail,
            },
            {
                path: "/creatorVideos",
                component: CreatorVideos,
            },
            {
                path: "/calendar",
                component: MarketCalendar,
            },
            {
                path: "/kline",
                component: Kline,
            },
        ]
    },
];



