import { Ajax } from '../utils/http';

export class CollectionListService {

    public async send(): Promise<CollectionItem[]> {
        let response: CollectionItem[];
        try {
            response = await new Ajax<CollectionItem[]>('/creator/videoCollectList',).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


/*Data*/
export interface CollectionItem {
    id: string;
    name: string;
    userid: string;
    parentid: string;
    isdelete: string;
    createtime: string;
    updatetime: string;
  }
  