import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { Sub_Bg_Img } from '../../images/SubBg';
import { LoadingOutlined } from '@ant-design/icons';
import 'react-calendar/dist/Calendar.css';
import './Kline.scss';
import NoData from '../Stock/NoData';
import { klineStore } from '../../stores/KlineStore';

export const Kline = observer(() => {
  useEffect(() => {
    klineStore.getData();
  }, []);
  const tabs = [{ text: "牛", id: 0 },
  { text: "中", id: 1 },
  { text: "熊", id: 2 }]
  return (
    <div className="calendarWrapper">
      <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-50px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title">K线形态</div>
          <div className="subTitle">为广大投资者提供对澳洲资本市场的独到见解、深度分析</div>

        </div>
      </header>
      <div className="calendarContentWrapper">
        <div className="topNav">
          <div className="menu">
            {
              tabs.map((r) => {
                return <div className="title" style={{ color: 0 === r.id ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                  // window.scrollTo(0, 0);
                }}>
                  {r.text}
                </div>
              })
            }

          </div>
        </div>
        <div className="newsContent">
          {klineStore.loading ? <div className="loading">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
          </div> : klineStore.list.map((n, i) => <div key={i} className="item">
            <div className="left">
              <div className="title">{n.pattern_name}.</div>
              <div className="tit-pic">
               {n.img_url && <img src={n.img_url} />
              }              </div>
            </div>
            <div className="content" dangerouslySetInnerHTML={{ __html: n.description.ch.replace(/\n/g, "<br/><br/>") }}></div>
           
          </div>)}

        </div>


      </div>
    </div>
  )
});