import { About_Bg_left } from '../../images/AboutbgLeft';
import { About_Bg_right } from '../../images/AboutbgRight';
import { About_Bg_top } from '../../images/AboutbgTop';
import { Img_Contact_QR1, Img_Contact_QR2 } from '../../images/ContactQr';
import './AboutUs.scss';

export const AboutUs = () => {
    return (
        <div className="AboutUsPage">
            <div className="headBg" style={{ backgroundImage: `url('${About_Bg_top}')` }}>
                <div className="info">
                <div className="headContent">
                    <div className="left" style={{ backgroundImage: `url('${About_Bg_left}')` }}>
                        <div className="title">
                            Our Story
                        </div>
                        <div className="content">
                            <p>Founded in 2018, Oli Capital is a leading Sydney-based
                                financial services and fund management company. We specialise in capital raising for primary and secondary markets, brokerage services and fund management.. </p>
                            <p>Oli Capital is a boutique fund management company that manages wholesale funds that invest in both Australian and international assets. Our fund management team has extensive experience in listed equity investment across the major exchanges globally.</p>
                        </div>
                    </div>
                </div>

                <div className="contact">
                            <div className="contactTitle">
                                Contact Us
                            </div>
                            <div className="content">
                                <div className="subTitle">Address</div>
                                <div className='item'>Suite 1103, 66 Goulburn St
                                    Sydney NSW 2000</div>
                                <div className="subTitle">Emial</div>
                                <div className='item'>info@olicapital.com.au</div>
                                <div className="subTitle">Tel</div>
                                <div className='item'> (02) 8288 9086</div>
                                <div className="wechatTitle">WeChat: 微信 (扫描下方二维码)</div>
                                <div className="wechat">
                                    <div className="customer">
                                        <img src={Img_Contact_QR1} alt="" />
                                        <div>客服号</div>
                                    </div>
                                    <div className="customer">
                                        <img src={Img_Contact_QR2} alt="" />
                                        <div>微信公众号
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
           
                </div>
                   
            </div>

        </div>
    )
}