import { Ajax } from '../utils/http';

export class PublishNewsService {
    private data: Object;
    constructor(_data: Object,) {
        this.data = _data;
    }

    public async send() {
        let response;
        try {
            response = await new Ajax('/creator/saveArticleDraft', this.data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}
