export class CreateCollectService {
    private title: string;
    constructor(_title: string) {
        this.title = _title;
    }

    public async send() {
        let data = {
            name: this.title,
        }
         console.log("data",data)
        try {
            await new Ajax('/creator/saveVideoCollect', data).post();
        } catch (e) {
            throw e;
        }
    }
}
