import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import { CreatorVideoData, CreatorVideoService } from "../services/CreatorVideoService";
 
class CreatorVideoStore {
  public list: CreatorVideoData[] = [];
  public loading: boolean = false;
  private _page = 1;
  public loadingMore: boolean = false;
  private noMore: boolean = false;
  public selectedItem?: CreatorVideoData;

  constructor() {
    makeAutoObservable(this);
  }

  public async getData(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: CreatorVideoData[];

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
      response = await new CreatorVideoService(this._page).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.length === 0;
      this.list.push(...response);
    } else {
      this.list = response;
    }
  }



}

const creatorVideoStore = new CreatorVideoStore();

export { creatorVideoStore };