import '../Videos/Videos.scss';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-awesome-slider/dist/styles.css';
import Icons from '../../images/images';
import AwesomeSlider from 'react-awesome-slider';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import eventBus from '../../utils/eventBus';
import { Icon_Play } from '../../images/icons/Play';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import { Sub_Bg_Img } from '../../images/SubBg';

const AutoplaySlider = withAutoplay(AwesomeSlider);

export const CreatorVideos = observer((props: any) => {
    useEffect(() => {
        creatorVideoStore.getData();
    }, []);
    let list: any = creatorVideoStore.list;
    let left = list.length % 4;
    for (let i = 0; i < left; i++) {
        list.push(undefined);
    }
    eventBus.addListener('toBottom', () => {
        creatorVideoStore.getData(true);
    })
    return (
        <div className="videoPage creatorVideoPage">
          <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-50px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title">OLI 创作者视频</div>
          {/* <div className="subTitle">全球金融市场的每日独家观点及意见，针对多个热门话题、热门产业进行全面的研究与深入的剖析.</div> */}

        </div>
      </header>
            <div className="videoWrapper">
                <ul className="content">
                    {creatorVideoStore.loading ? <div className="loading">
                        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                    </div> : creatorVideoStore.list.map((item, idx) => {
                        if (!item) {
                            return <li className="item"></li>
                        }
                        return <li key={props.key} role="listitem" className="collection-item-2 w-dyn-item w-col w-col-4 videoItem" onClick={() => {
                            creatorVideoStore.selectedItem = item;
                        }}><a
                            href="javascript:;" className="link-block-4 w-inline-block">
                                <div className="div-block-39">
                                    <div style={{ backgroundImage: `url(${item.thumb})` }}
                                        className="div-block-46">
                                            <img
                                            src="https://uploads-ssl.webflow.com/5d1f262a719f5884d8fee399/5fb47b1dacc0cadae640ead0_white_weave_bottom_div.svg"
                                            loading="lazy" alt="" className="image-23" /></div>
                                    <div className="div-block-40">
                                        <p className="paragraph-8">{item.collectname}.</p>
                                        <div className="div-block-41">
                                            <div className="view-left">
                                                <EyeOutlined />
                                                <div className="viewCount">{0}</div>
                                                <StarOutlined style={{ color:  'inherit' }} />
                                                <HeartOutlined style={{ color:  'inherit' }} />
                                            </div>
                                            <div>
                                                {/* <div className="text-block-9">{item..slice(0, 10)}</div> */}
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </a>
                            <div className="mask">
                                <img
                                    src={Icon_Play}
                                    loading="lazy" alt="" className="image-23" />

                            </div>
                        </li>
                    })
                    }
                </ul>
                {creatorVideoStore.loadingMore && <div className="loadmore">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
                </div>}
            </div>
            {
                creatorVideoStore.selectedItem && <div className="videoContent">
                    <div className="tabs">
                        <img className="back" src={Icons.close} alt="" onClick={() => {
                            creatorVideoStore.selectedItem = undefined;
                        }} />

                        {/* <div className="items">
                            <div className="like">
                                <img src={true ? Icons.icon_like_s : Icons.icon_like} alt="" onClick={() => {
                                    // researchDetailStore.like();
                                }} />
                                <div className="count">{22}</div>
                            </div>
                            <img className='follow' src={true ? Icons.follow_s : Icons.follow} alt="" onClick={() => {
                                // researchDetailStore.follow();
                            }} />
                        </div> */}
                    </div>
                    <div className="content">
                        <div className='videoPlayer'>
                            <AwesomeSlider
                                onFirstMount={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    if(video)
                                     video.autoplay = true;
                                }}
                                onTransitionStart={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    if (video !== null && !video.paused) {
                                        video.pause();
                                    }
                                }}
                                onTransitionEnd={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.play();
                                }}
                                bullets={false}
                                // selected={creatorVideoStore.selectedItem.list.map((l) => l.id).indexOf(creatorVideoStore.selectedItem.id)}
                                media={creatorVideoStore.selectedItem.list.map((item) => { return { source: item.url } })}
                            />
                            <div className="title">
                                {creatorVideoStore.selectedItem.title}
                            </div>
                        </div>
                        {/* <div className="info">

                            <div className="videoList">
                                {creatorVideoStore.selectedItem.list.map((item, idx) => {
                                    if (!item) {
                                        return <li className="item"></li>
                                    }
                                    return <li className="item" key={idx} onClick={() => {
                                        creatorVideoStore.selectedItem = item;
                                    }}>

                                        <LazyLoadImage
                                            alt={item.title}
                                            effect="opacity"
                                            src={item.thumb} />
                                        <div className="title">{item.title}</div>
                                    </li>
                                })}
                            </div>
                        </div> */}

                    </div>


                </div>
            }

        </div >
    )
});